html {
  --header-nav-color: #00acdc;
}

body, ul {
  color: #000;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header_logo {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.logo {
  width: 100%;
}

.header_menu {
  justify-content: space-between;
  align-items: baseline;
  list-style-type: none;
  display: flex;
}

.menu_item {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
}

.menu_item_link {
  color: inherit;
  padding: 23px 0;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  position: relative;
}

.menu_item_link:hover, .menu_item_link.is-active {
  color: var(--header-nav-color);
}

.is-active:after {
  content: "";
  background-color: var(--header-nav-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.menu_item:last-child {
  margin: 0;
}

